import React from "react";
import { useSelector } from "react-redux";

const Home = ()=>{
    
    const breeds = useSelector(state => state.breeds)
    return(
      <div>
          <h2>Hola</h2>
            {
             breeds?.map(breed => {
            return<p>{breed.name}</p>
            })
        }
      </div>
    )
}

export default Home;